<template>
  <div class="login">
    <div class="mui-content">
      <form class="mui-input-group">
        <div class="mui-input-row">
          <label>账号</label>
          <input
            v-model="loginForm.username"
            type="text"
            class="mui-input-clear mui-input"
            placeholder="请输入账号"
          >
        </div>
        <div class="mui-input-row">
          <label>密码</label>
          <input
            v-model="loginForm.password"
            type="password"
            class="mui-input-clear mui-input"
            placeholder="请输入密码"
          >
        </div>
      </form>
      <div class="mui-content-padded">
        <button @click="login" type="button" class="mui-btn mui-btn-block mui-btn-primary">登录</button>
        <div class="link-area">
          <a @click="register">还没有账号？前往注册</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    login() {
      //   登录功能 此处代码可以放置在store中 使页面更简洁
          // 判断是否为空
      if (
        this.loginForm.username.trim() === "" &&
        this.loginForm.password.trim() === ""
      ) {
        this.$toast("用户名和密码不能为空");
        this.loginForm.username = "";
        this.loginForm.password = "";
      } else {
        // 获取用户账号 用于检验
        this.$http
          .get("posts", {
            params: {
              username: this.loginForm.username,
              password: this.loginForm.password
            }
          })
          .then(res => {
            // console.log(res);
            // 存储获取的用户名和密码
            let username = res.data[0].username;
            let password = res.data[0].password;
            if (res.status === 200 || res.status < 300) {
              // 检验用户信息
              if (
                this.loginForm.username === username &&
                this.loginForm.password === password
              ) {
                this.$toast("登录成功");
                // 存储数据到store中
                this.$store.commit("user/setUser", res.data[0]);
                //  console.log(res.data[0]);
                //  console.log(this.$store.state.user.username);
                // 登陆成功跳转到我的页面,
                this.$router.push("/user");
                //进行本地存储localStorage存储数据 保存登录状态
                this.$auth.setUserId(res.data[0].id);
                this.$auth.setUserName(res.data[0].username);
              }
            }
          })
          .catch(err => {
            console.log(err);
            this.$toast("用户或密码不存在");
          });
      }
    },
    register() {
      //   注册 跳转到注册路由组件
      this.$router.push("/user/register");
    }
  }
};
</script>
<style lang="scss" scoped>
.mui-input-group {
  margin-top: 10px;
  background-color: transparent;
}
.mui-input-group label {
  width: 22%;
}
.mui-input-row:last-child {
  background: #fff;
}
.mui-input-row {
  margin-top: 20px;
  background: #fff;
}
.mui-input-row label ~ input,
.mui-input-row label ~ select,
.mui-input-row label ~ textarea {
  width: 78%;
}
.link-area {
  display: block;
  margin-top: 25px;
  text-align: center;
}
.mui-content-padded {
  margin-top: 30px;
}
</style>

