                
<template>
  <!--同个路由页面实现新增和修改地址俩个功能   借助路由传参来判断 -->
  <form class="mui-input-group">
    <div class="mui-input-row">
      <label>收件人</label>
      <input type="text" class="mui-input-clear" placeholder="请输入用户名" v-model="form.user">
    </div>
    <div class="mui-input-row">
      <label>联系方式</label>
      <input type="text" class="mui-input-clear" placeholder="请输入手机号" v-model="form.phone">
    </div>
    <div class="mui-input-row">
      <label>所在地区</label>
      <input
        type="text"
        class="mui-input-clear"
        placeholder="请输入所在地区"
        v-model="form.area"
        @click="choose"
      >
      <div class="divwrap">
        <!-- 遮罩层 -->
        <div class="mask" v-show="show" @click="choose"></div>
        <!--province city area选中省\城\区时触发的回调函数-->
        <v-distpicker
          type="mobile"
          v-show="show"
          :province="addressInfo.province"
          :city="addressInfo.city"
          :area="addressInfo.area"
          @province="onChangeProvince"
          @city="onChangeCity"
          @area="onChangeArea"
        />
      </div>
    </div>
    <div class="mui-input-row">
      <label>详细地址</label>
      <input type="text" class="mui-input-clear" placeholder="请输入详细地址" v-model="form.det">
    </div>
    <div class="mui-button-row">
      <button type="button" class="mui-btn mui-btn-primary mui-btn-block" @click="save">确认</button>
    </div>
    <div class="mui-button-row" v-show="id">
      <button type="button" class="mui-btn mui-btn-danger mui-btn-block remove" @click="remove">删除</button>
    </div>
  </form>
</template>
<script>
import VDistpicker from "v-distpicker"; //地区选择器插件组件
export default {
  props: ["id"],//编辑地址的请求id
  components: {
    "v-distpicker": VDistpicker
  },
  data() {
    return {
      //收货地址信息
      form: {
        user: "",
        phone: "",
        area: "",
        det: ""
      },
      show: false, //是否显示遮罩层和地址选择器
      // 选择所在地区,省/城/区的信息
      addressInfo: {
        province: "",
        city: "",
        area: ""
      },
      address: []
    };
  },
  mounted() {
    // 有传递prarams参数则是要修改地址 并通过id获取要修改的地址 并呈现到修改页面中去
    if (this.id) {
      this.$http
        .get("posts", { params: { id: this.id } })
        .then(sucess => {
          //   console.log(sucess.data[0]);
          this.form = sucess.data[0];
        })
        .catch(err => {
          this.$toast("出错了");
        });
    }
  },
  methods: {
    save() {
      // 检查
      if (this.form.user.trim().length > 5) {
        this.$toast("用户名不能大于5位");
        return;
      } else if (this.form.user.trim() === "") {
        this.$toast("用户名不能为空");
        return;
      }
      if (
        !/^(13[0-9]|14[5-9]|15[0-35-9]|16[25-7]|17[0-8]|18[0-9]|19[0135689])[0-9]{8}$/.test(
          this.form.phone
        )
      ) {
        this.$toast("手机号格式不正确");
        return;
      } else if (this.form.phone.trim() === "") {
        this.$toast("手机号不能为空");
        return;
      }
      if (this.form.area.trim() === "") {
        this.$toast("地址不能为空");
        return;
      }
      if (this.form.det.trim() === "") {
        this.$toast("详细地址不能为空");
        return;
      }
      //  保存新增地址
      if (!this.id) {
        // 提交地址
        this.$http
          .post("posts", this.form)
          .then(success => {
            // console.log(success.data);
            if (success.status >= 200 || success.status < 300) {
              this.$toast("添加成功");
              this.$router.replace({ name: "address" });
              // 将地址传递并保存到store
              this.$store.commit("user/setAddress", success.data);
            }
          })
          .catch(err => {
            this.$toast(err);
          });
      }
      //params路由参数存在 则是要进行地址编辑
      if (this.id) {
         //  传递给store 保存修改的收货地址
        this.$store.commit("user/EditAddress", this.form);
        // 跳转到地址列表
        this.$router.replace("/Address");
      }
    },
    remove(){
      // 将删除的地址id传递
        this.$store.commit('user/RemoveAddress',this.id)
        this.$router.replace('/Address')
    },
    // v-distpicker
    // 钩子函数
    //province city area选中省\城\区时触发的回调函数
    // 保存省/城/区的值
    onChangeProvince(data) {
      this.addressInfo.province = data.value;
    },
    onChangeCity(data) {
      this.addressInfo.city = data.value;
    },
    onChangeArea(data) {
      this.addressInfo.area = data.value;
      // 将选择的省/城/区的城赋值给from表单中的area进行显示
      this.form.area = `${this.addressInfo.province}-${this.addressInfo.city}-${
        this.addressInfo.area
      }`;
      this.show = false;//关闭选择器和遮罩层
    },
    choose() {
      this.show = !this.show;//显示遮罩层和地区选择器
    }
  }
};
</script>
<style lang="scss" scoped>
label {
  text-align: left;
}
.mui-input-group .mui-button-row {
  height: auto;
}
.mui-input-clear {
  font-size: 14px;
}
.mui-input-group:before {
  list-style: none;
}
.mui-button-row {
  margin-top: 20px;
  button {
    margin: 20px 0;
    padding: 10px 0;
  }
}
.tit {
  color: #999;
}
input::-webkit-input-placeholder {
  color: #999;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #999;
}
.divwrap > .mask {
  background: #000;
  opacity: 0.3;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.divwrap > .distpicker-address-wrapper {
  color: #999;
  background: #fff;
  border-top: 1px solid #ccc;
  z-index: 1;
  height: 300px;
  overflow-y: auto;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.mui-button-row {
  margin-top: 0;
  padding-top: 0;
}
</style>

