function setShop(name, item) {
    localStorage.setItem(name, JSON.stringify(item))
}
function getShop(name) {
    return JSON.parse(localStorage.getItem(name) || '[]')
}
let state = {
    //[ {id:'',num:0,done:false}]
    car: getShop('car'),//购物车
    buy: getShop('buy') //结算车 
}
let mutations = {
    addShopCar(state, goodsinfo) {
        //加入购物车 购物车中有这个商品则累加 没有则添加
        let flag = false //定义变量来判断   
        //遍历元素判断是否满足条件 
        // 加入购物车的商品和购物车中已有的商品 俩者的id比较
        state.car.some(item => {
            if (item.id === goodsinfo.id) {
                // 相同商品累加
                item.count += parseInt(goodsinfo.count)
                flag = true //有
                return true
            }
        })
        // 没有 加入购物车
        if (!flag) {
            state.car.push(goodsinfo)
        }

        // 重新进行本地存储
        setShop('car', state.car)
    },
    updataGoodsInfo(state, goodsinfo) {
        //购物车  用于更新商品数量 
        state.car.some(itme => {
            // 相同商品进行数量累加
            if (itme.id === goodsinfo.id) {
                itme.count = parseInt(goodsinfo.count)
                return true
            }
        })
        // 保存
        setShop('car', state.car)
    },
    upDataDone(state,id) {
        // 查找   修改商品是否选中
        state.car.some(item => {
            // 根据传递的id 对购物车中的商品id进行比较
            if (item.id === id) {
                // 相同修改商品状态
                item.done = !item.done
                return true
            }
        })
        setShop('car', state.car)
    },
    deleteGoods(state, goodsID) {
        //根据用户删除的goodsID商品id进行删除 goodsID是想要删除的商品id
        // 被过滤的是不用删除的商品 返回一个新数组
        state.car = state.car.filter(item => {
            // 商品id不等于传递过来的id 
            return item.id !== goodsID
        })
        setShop('car', state.car)
    },
    setBuy(state,buyNow) {
        // buyNow是立即结算的商品默认为ture
        state.buy = []  //勾选的商品 加入到结算车
        if(buyNow){
            // 加入结算车
            state.buy.push(buyNow)
        }
        state.car.forEach(item => {
            // 商品状态为true的加入结算车
            if (item.done) {
                state.buy.push(item)
            }
        })
        setShop('buy', state.buy)
    },
    cancelOrder(state, id) {
        // 订单页面取消订单 id为点击取消的商品id 
        state.buy.forEach(item => {
            // 取消订单的商品状态为false
                if (!item.done) {
                    // 修改cancel的值
                    item.cancel = "已取消"
                }
        })
        setShop('buy', state.buy)
    }
}
let getters = {
    // 获取商品购买数量
    getGoodsCount(state) {
        // 添加商品id和数量作为属性
        let counts = {} //{商品id：商品数量}
        state.car.forEach(element => {
            counts[element.id] = element.count
        });
        return counts
    },
    //获取所有选中的商品数量
    getSelectCounts(state) {
        let counts = 0
        state.car.forEach(item => {
            // 累加所有选中的商品数量
            if (item.done) {
                counts += item.count
            }
        })
        return counts
    },
    // 获取所有勾选商品的总价 商品状态为true
    getGoodsPrice(state) {
        let price = 0
        state.car.forEach(item => {
            if (item.done) {
                // 累加所有选中商品的价格
                price += parseInt(item.price * item.count)
            }
        })
        return price
    },
   
    getBuyCounts(state) {
        let counts = {} //同件商品的数量
        // {商品id:商品数量}
        state.buy.forEach(item => {
            // 根据id来进行同件商品的数量累加
            counts[item.id] = item.count
        })
        return counts
    },
    // 获取结算商品的信息
    getBuy(state) {
        state.buy.forEach(item => {
            // 跳转到订单页面默认是显示取消订单
            if (item.done) {
                item.cancel = "取消订单"
            }
        })
        setShop('buy', state.buy)
        return state.buy
    },

}
let actions = {
}
export default {
    namespaced: true,
    state,
    mutations,
    getters,
    actions,

}