<template>
  <div class="photo.con">
    <!-- 顶部横向滑动菜单 -->
    <div
      class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted"
    >
      <div class="mui-scroll">
        <!-- 在这里使用tap（只有mui组件才可以使用）tap手指点击事件 -->
        <a
          :class="['mui-control-item', item.pid===cateId?'mui-active':'']"
          v-for="item in cates"
          :key="item.pid"
          @tap="getPhotosInfo(item.pid)"
        >{{item.title}}</a>
      </div>
    </div>
    <!-- 图片列表区域 -->
    <ul class="photo-list">
      <!-- <li v-for="item in photoList" :key="item.pid">
        <router-link :to="{ name: 'photo_show', params: { id: item.pid }}">
      <img v-lazy="item.img_url">-->
      <!-- 使用图片懒加载 -->
      <!-- <div class="info">
            <h1 class="info-title">{{ item.title }}</h1>
            <div class="info-body">{{ item.zhaiyao }}</div>
          </div>
        </router-link>
      </li>-->
      <!--默认a标签 tag将路由标签解析为指定标签保留跳转功能 -->
      <router-link
        v-for="item in photoList"
        :key="item.pid"
        :to="{ name: 'photo_show', params: { id: item.pid }}"
        tag="li"
      >
        <img v-lazy="item.img_url">
        <!-- 使用图片懒加载 -->
        <div class="info">
          <h1 class="info-title">{{ item.title }}</h1>
          <div class="info-body">{{ item.zhaiyao }}</div>
        </div>
      </router-link>
    </ul>
  </div>
</template>
<script>
import mui from "../../lib/mui/js/mui.min.js";
export default {
  data() {
    return {
      cates: [], //保存导航分类标题
      photoList: [], //保存图片的信息
      cateId: 0 //保存用户点击图片对应的id  用于对比 点击显示高亮效果
    };
  },
  mounted() {
    // 初始化scroll滑动控件
    mui(".mui-scroll-wrapper").scroll({
      deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
    });
    // 获取导航标题
    this.$http
      .get("posts/777")
      .then(sucess => {
        // console.log(sucess);
        if (sucess.status >= 200 || sucess.status < 300) {
          this.cates = sucess.data.msg;
        }
      })
      .catch(err => {
        this.$toast("加载失败");
      });
    this.getPhotosInfo(7770); //默认获取全部
  },
  methods: {
    getPhotosInfo(cateId) {
      this.cateId = cateId;
      // 获取图片的详细信息
      this.$http
        .get("posts", { params: { id: cateId } })
        .then(sucess => {
          console.log(sucess.data[0].msg);
          if (sucess.status >= 200 || sucess.status < 300) {
            this.photoList = sucess.data[0].msg;
          }
        })
        .catch(err => {
          this.$toast("加载失败");
        });
    }
  }
};
</script>
<style lang="scss" scoped>
* {
  touch-action: pan-y; /*去除警告*/
}
.photo-list {
  list-style: none;
  margin: 0;
  padding: 10px;
  padding-bottom: 0;
  li {
    background-color: #ccc;
    text-align: center;
    margin-bottom: 5px;
    box-shadow: 0 0 9px #999;
    position: relative;
    img {
      width: 100%;
      vertical-align: middle;
    }
    img[lazy="loading"] {
      width: 40px;
      height: 300px;
      margin: auto;
    }
    .info {
      color: white;
      text-align: left;
      position: absolute;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.4);
      max-height: 84px;
      .info-title {
        font-size: 14px;
      }
      .info-body {
        font-size: 13px;
      }
    }
  }
}
</style>



