<template>
  <div class="category">
    <!-- 纵向滚动菜单列表 -->
    <div class="menu-left">
      <ul class="menu-bar">
        <li
          v-for="(item, index) in menu_bar"
          :key="index"
          :class="{active:index==currentIndex}"
          @click="showActive(index)"
        >{{item.name}}</li>
      </ul>
    </div>
    <!-- 商品分类 -->
    <div class="menu-rigth" ref="imgList">
      <ul class="menu_item">
        <li v-for="menu_item in menu_bar" :key="menu_item.id" class="item">
          <h4>{{menu_item.name}}</h4>
          <ul class="menu_item_list">
            <li
              class="menu_item_list_imgs"
              v-for="(item_list, index) in menu_item.imagesList"
              :key="index"
            >
              <!-- 路由参数是用于商品列表请求数据的id -->
              <routerLink
                :to="{name:'goodsList',params:{category_id:menu_item.id}}"
                class="image_url"
              >
                <div>
                  <img :src="item_list.image">
                </div>
                <span>{{item_list.phone_name}}</span>
              </routerLink>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
// 导入滚动插件
import BScroll from "better-scroll";
export default {
  data() {
    return {
      menu_bar: [], //保存商品菜单栏信息和分类商品
      currentIndex: 0, //   记录用户当前点击菜单的索引 用于高亮
      rightTops: [] //存储li元素的屏幕高度 用于滑动到对应模块时对应菜单栏项高亮
    };
  },
  methods: {
    showActive(idx) {
      //点击菜单栏滑动到对应的分类模块
      this.currentIndex = idx; //用户点击的菜单栏索引
      let y = -this.rightTops[idx]; //获取点击滑动到对应的模块的高度
      this.rightScroll.scrollTo(0, y); //借助插件的scrollTo事件来触发 前俩给参数为x y坐标
    },
    // 使用插件BScroll
    // 滚动功能
    initBScroll() {
      this.leftScroll = new BScroll(".menu-left", {
        click: true,
        mouseWheel: true
      });
      this.rightScroll = new BScroll(".menu-rigth", {
        click: true,
        // 允许鼠标滚动
        mouseWheel: true,
        probeType: 3 //同时监听鼠标拖拽
      });
    },
    initBScrollTop() {
      // 获取li标签
      const item = this.$refs.imgList.querySelectorAll(".item");
      const TopsArry = [];// 存储每个li元素的高
      let top = 0;
      TopsArry.push(top); //默认值为0 顶部
      for (let idx = 0; idx < item.length; idx++) {
        // offsetTop偏移量 距离最近的父级元素的高度 clientHeight元素自身的屏幕高度
        // console.log(item[idx].offsetTop);
        //  console.log(item[idx].clientHeight);
        //   top +=item[idx].offsetTop///存储top值
        top += item[idx].clientHeight;//累加 li元素自身的屏幕高度
        TopsArry.push(top);//存储li元素的屏幕高度
      }
      //   console.log(this.itemsTop);
      this.rightTops = TopsArry;//赋值存储
      // 为rightScroll注册监听事件 实现需求：滚动模块时对应的菜单栏中菜单高亮
      this.rightScroll.on("scroll", pos => {
        let scrollY = Math.abs(pos.y); //滚动的Y坐标的绝对值 向下滑动页面是往上(被卷起来)的所以y是负值
        for (let i = 0; i < this.rightTops.length - 1; i++) {
          //   判断滚动高度是否滚动到某个li模块 对应的菜单栏项高亮
          if (scrollY > TopsArry[i] && scrollY < TopsArry[i + 1]) {
            // 将索引负值给currentIndex让它高亮
            this.currentIndex = i;
          }
        }
      });
    }
  },
  mounted() {
    // 提示
    this.$indicator.open({ text: "加载中..." });
       //   获取商品
    this.$http.get("posts", { params: { id: 66 } }).then(res => {
      this.$indicator.close();
      //    console.log(res.data[0].shop_item);
      this.menu_bar = res.data[0].shop_item;
    });
  },
  watch: {
    //检测menu_bar数据发生改变就重新执行
    menu_bar() {
      //   $nextTick页面渲染完毕调用 类似onload
      this.$nextTick(() => {
        this.initBScroll();
        this.initBScrollTop();
      });
    }
  }
};
</script>
<style lang="scss" scoped>
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.active {
  background-color: red;
  color: white;
}
h4 {
  text-align: left;
}
.menu_item_list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  .menu_item_list_imgs {
    width: 33.3%;
    img {
      width: 100%;
    }
    span {
      color: gray;
    }
  }
}

.category {
  height: 100vh;
  display: flex;
  justify-content: flex-start;
}
.menu-left {
  height: 100%;
  .menu-bar {
    padding: 0;
    margin: 0;

    height: 100%;
    width: 100px;
    background-color: lavender;
    li {
      display: flex;
      flex-direction: column;
      justify-content: center;

      height: 50px;
      width: 100px;
    }
  }
}
</style>
