<template>
  <div class="goods-list">
    <div class="goods-item" v-for="(item,index) in goodsListArry" :key="index">
      <routerLink :to="{name:'GoodsInfo',params:{pid:item.pid}}">
        <img :src="item.image">
        <h1 class="title">商品{{ item.name }}</h1>
        <p class="info">
          <span class="price">¥{{ item.price }}</span>
          <span class="sell">剩余 {{ item.number }} 件</span>
        </p>
      </routerLink>
    </div>
    <mt-button
      size="large"
      v-show="this.goodsListArry.length !==0"
      class="more"
      @click.once="getMore"
    >加载更多</mt-button>
  </div>
</template>
<script>
export default {
  // 路由参数
  props: ["category_id"],
  data() {
    return {
      goodsListArry: [],
      id: 0
    };
  },
  created() {
    this.getGoodsList(0);
  },
  methods: {
    getGoodsList(more) {
      // 获取商品列表
      this.$indicator.open({ text: "图片加载中" });
      //分类页面请求数据的id  和 商品列表请求数据的id是分类页面请求数据的id+01
      this.id = this.category_id + "0" + "1";
      this.$http
        .get("posts", { params: { id: this.id } })
        .then(res => {
          this.$indicator.close();
          console.log(res);
          // console.log(res.data[0].spLists);
          if (more===0) {
            this.goodsListArry = res.data[0].spLists;
          } 
          if(more===1){
            // 显示更多商品
            this.goodsListArry = this.goodsListArry.concat(res.data[0].moreSp);
            this.$toast("已经到底了！");
          }
          // 商品为空
          if (res.data[0].spLists.length === 0) {
            this.$toast("商品列表数据为空" + this.id);
          }
        })
        .catch(err => {
          this.$toast("出错了");
        });
    },
    getMore() {
      // 获取更多商品
      this.getGoodsList(1);
    }
  }
};
</script>
<style lang="scss" scoped>
.goods-list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 10px;
  .goods-item {
    width: calc(calc(100% / 2) - 10px);
    margin: 10px 10px 0 0;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 10px;
    padding: 10px;
    img {
      width: 100%;
    }
    .title {
      font-size: 14px;
      color: #333;
      margin: 10px 0;
    }
    .info {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0;
      .price {
        color: red;
        font-weight: bold;
        font-size: 16px;
      }
      .sell {
        font-size: 13px;
      }
    }
  }
  .more {
    margin: 15px 0;
    font-size: 14px;
  }
}
</style>


