<template>
    <div class="newsinfo-container">
    <!-- 大标题 -->
    <h1 class="title">{{ newsinfolist.title }}</h1>
    <!-- 子标题 -->
    <p class="newsinfo-nav">
      <span>发表时间：{{ newsinfolist.add_time | dateFormat }}</span>
      <span>点击：{{newsinfolist.click}}次</span>
    </p>
    <hr>
    <!-- 内容区域 -->
    <div class="content" v-html="newsinfolist.content">
    </div>
  </div>
</template>
<script>
export default {
    props:['id'],
    data() {
        return {
            newsinfolist:{}
        }
    },
    mounted() {
        // 获取新闻详情
        console.log(this.id);
        this.$http.get(`posts`,{params:{id:this.id}}).then(sucess=>{
            // console.log(sucess);
            this.newsinfolist=sucess.data[0]
        }).catch(err=>{
            this.$toast('获取失败')
        })
    },
}
</script>
<style lang="scss" scoped>
.newsinfo-container {
  padding: 0 5px;
  .title {
    font-size: 16px;
    text-align: center;
    margin: 10px 0;
    color: orangered;
  }
  .newsinfo-nav {
    display: flex;
    justify-content: space-between;
    color: #26a2ff;
  }

}
</style>


