<template>
  <div class="photoinfo-container">
    <!-- 大标题 -->
    <h1 class="title">{{ newsinfolist.title }}</h1>
    <!-- 子标题 -->
    <p class="photoinfo-nav">
      <span>发表时间：{{ newsinfolist.add_time | dateFormat }}</span>
      <span>点击：{{newsinfolist.click}}次</span>
    </p>
    <hr>
      <!-- 缩略图区域 -->
    <div class="thumbs">
      <!-- vue-preview图片预览插件 -->
        <!--俩个标签属性 slides要预览的图片数组  @close关闭后执行的事件函数-->
        <!-- 预览的图片要设置宽w高h  和设置msrc属性是正常显示的图片路径-->
      <vue-preview :slides="lists"></vue-preview>
    </div>
    <!-- 内容区域 -->
    <div class="content">
      <p>
        <span>{{newsinfolist.content}}</span>
      </p>
    </div>
  </div>
</template>
<script>
export default {
  props: ["id"],//图片的请求id编号 路由参数params
  data() {
    return {
      newsinfolist: {}, //图片详细
      lists: [] //预览图片
    };
  },
  mounted() {
    // 获取图片详情
    this.$http.get("posts", { params: { id: this.id } }).then(sucess => {
      console.log(sucess);
      if (sucess.status >= 200 || sucess.status < 300) {
        this.newsinfolist = sucess.data[0].msg;
      }
    });
    this.handlerColse();
  },
  methods: {
    handlerColse() {
        this.lists=[]
      this.$http.get("posts", { params: { id: this.id } }).then(sucess => {
        console.log(sucess);
        if (sucess.status >= 200 || sucess.status < 300) {
          // 给图片添加宽高 调整预览图
          // console.log(sucess.data);
          sucess.data.forEach(item => {
            item.msg.img_url.w = 600;
            item.msg.img_url.h = 400;
            item.msg.img_url.msrc= item.msg.img_url.src //msrc属性是组件正常显示图片的src
            // 添加到预览图片数组中
            this.lists.push(item.msg.img_url);
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" >
.photoinfo-container {
  padding: 0 5px;
  .title {
    font-size: 16px;
    text-align: center;
    margin: 10px 0;
    color: #26a2ff;
  }
  .photoinfo-nav {
    display: flex;
    justify-content: space-between;
    color: gray;
  }
  .content {
    text-align: left;
    span {
      font-size: 15px;
      color: black;
    }
  }
    .thumbs {
    .my-gallery {
      width: 100%;
      display: flex;
      flex-flow: row wrap;
    }
    figure {
      margin: 0.2em;
    }
    img {
      width: 5.6em;
      box-shadow: 0 0 8px #999;
    }
  }
}
</style>

