// 搭建vuex环境
// 导入vue vuex
import Vue from 'vue'
import Vuex from 'vuex'
// 导入要存储的数据
import user from './modules/user.js'
import shopcar from './modules/shopcar.js'
//使用
Vue.use(Vuex)
// 创建vuex实例并暴露
export default new Vuex.Store({
    // 模块化
    modules:{
        user,
        shopcar
    }
})