<template>
  <div>
                          <!-- 登录成功显示的我的页面 -->
                          <!-- 调用存储在本地的用户id未判断条件 -->
    <div class="member" v-if="showUser">
      <div class="header-con">
        <router-link to="/user/login" class="mui-navigate-right">
          <div class="user-info">
            <div class="avatar-con">
              <div class="avatar">
                <img src="../assets/images/avatar_default.png" class="image-info">
              </div>
            </div>
            <div class="person-con">
              <!-- 调用在本地存储中的用户名 可以防止刷新丢失数据保存登录状态后用户名的显示-->
              <span>{{$auth.getUserName()}}</span>
            </div>
          </div>
        </router-link>
      </div>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell mui-media">
          <div class="mui-navigate-right">
            <img class="mui-media-object mui-pull-left" src="../assets/images/avatar_default.png">
            <!-- 路由跳转 订单路由 -->
             <router-link to="/orderList">
                <div class="mui-media-body" >我的订单</div>
            </router-link>
          </div>
        </li>
        <li class="mui-table-view-cell mui-media">
          <div class="mui-navigate-right">
            <img class="mui-media-object mui-pull-left" src="../assets/images/avatar_default.png">
            <router-link to="/Address">
            <!-- 跳转到 地址路由组件 -->
              <div class="mui-media-body">收货地址</div>
            </router-link>
          </div>
        </li>
        <li class="mui-table-view-cell mui-media">
          <div class="mui-navigate-right">
            <img class="mui-media-object mui-pull-left" src="../assets/images/avatar_default.png">
            <div class="mui-media-body" @click="logout">退出登录</div>
          </div>
        </li>
      </ul>
    </div>
    <!-- 未登录显示的 我的页面 -->
    <div class="member" v-else>
      <div class="header-con">
        <router-link to="/user/login" class="mui-navigate-right">
          <div class="user-info">
            <div class="avatar-con">
              <div class="avatar">
                <img src="../assets/images/avatar_default.png" class="image-info">
              </div>
            </div>
            <div class="person-con">
              <span>登录 / 注册</span>
            </div>
          </div>
        </router-link>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showUser:this.$auth.getUserId()
    };
  },
  methods: {
    logout() {
      // 退出登录功能
      // 清空本地缓存和store中的数据
      this.$auth.clearUser("userID", "userName");
      this.$store.commit("user/clearUser", { id: 0, username: "" });
      this.$toast("退出成功");
      // 清空重新获取登录状态
      this.showUser=this.$auth.getUserId()
    },
 
  }
};
</script>
<style lang="scss" scoped>
a {
  color: gray;
}
.mui-table-view {
  margin-top: 15px;
}
.mui-table-view .mui-media,
.mui-table-view .mui-media-body {
  text-align: left;
  line-height: 45px;
}
.mui-table-view-cell:after {
  height: 0px;
}
.member {
  margin-bottom: 15px;
  .header-con {
    padding: 10px;
    background-color: #fff;
    .user-info {
      position: relative;
      overflow: hidden;
      width: 100%;
      height: 120px;
      background: linear-gradient(90deg, #28a2ff, #ffd787);
      box-shadow: 0 0.1rem 0.25rem #f8e3c6;
      .avatar-con {
        position: absolute;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
        .avatar {
          width: 60px;
          height: 60px;
          overflow: hidden;
          box-shadow: 0 2px 10px rgba(0, 0, 0, 15);
          border: 1px solid hsla(0, 0%, 100%, 0.4);
          border-radius: 50% 50%;
          .image-info {
            width: 100%;
            height: 100%;
          }
        }
      }
      .person-con {
        position: absolute;
        left: 90px;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
      }
    }
  }
}
</style>
