//搭建路由器环境
// 引入路由插件
import VueRouter from 'vue-router'
// 创建并暴露路由器实例

// 引入路由组件
import Home from '../pages/Home.vue'
import Category from '../pages/Category.vue'
import User from '../pages/User.vue'
import shopCar from '../pages/shopCar.vue'
import Login from '../pages/user/login.vue'
import Register from '../pages/user/register.vue'
import GoodsList from '../pages/goods/GoodsList.vue'
import GoodsInfo from '../pages/goods/GoodsInfo.vue'
import Address from '../pages/user/Address.vue'
import AddressEdit from '../pages/user/AddressEdit.vue'
import OrederCreate from '../pages/order/OrderCreate.vue'
import OrderList from '../pages/order/OrderList.vue'
import NewLists from '../pages/news/NewLists.vue'
import NewsInfo from '../pages/news/NewsInfo.vue'
import PhotoLists from '../pages/photo/PhotoLists.vue'
import PhotoInfo from '../pages/photo/PhotoInfo.vue'
const router= new VueRouter({
    // 路由配置
    routes:[
    //    根路径挑转到首页
        {
            path:"/",
            redirect:"/home",//重定向
        },
          // home路由
        {
             path:"/home",
             name:"home",
            component:Home,
            meta:{title:"首页"}
        },
        // 分类路由
        {
             path:"/category",
             name:"category",
             meta:{title:"分类"},
             component:Category
        },
        // 购物车路由
        {
            path:"/shopCar",
            name:"shopCar",
            component:shopCar,
            meta:{title:"购物车"}
        },
        // 我的路由
        {
            path:"/user",
            name:"user",
            component:User,
            meta:{title:"我的"},
        },
          // 登录路由组件
          {
            name:"login",
            path:"/user/login",
            component:Login,
            meta:{title:"登录"}
        },
           // 注册路由组件
           {
            name:"register",
            path:"/user/register",
            component:Register,
            meta:{title:"注册"}
        },
        // 商品分类列表路由
        {
            name:"goodsList",
            path:"/goodsList/:category_id",
            component:GoodsList,
            meta:{title:"商品列表"},
            props:true //通过props来接受params参数
        },
         // 商品详情
         {
            name:"GoodsInfo",
            path:"/GoodsInfo/:pid",
            component:GoodsInfo,
            meta:{title:"商品信息"},
           props:true
        },
        // 收货地址列表
        {
            name:"address",
            path:"/Address",
            component:Address,
            meta:{title:"收货地址列表"},
        },
        // 新增收货地址
        {
            name:"addressAdd",
            path:"/Address/add",
            component:AddressEdit,
            meta:{title:"新增收货地址"},
        },
        // 修改收货地址
        {
            name:"addressEdit",
            path:"/Address/edit/:id",
            component:AddressEdit,
            meta:{title:"修改收货地址"},
            props:true
        },
        // 结算订单页面
        {
            name:"orderCreate",
            path:"/order/create/:id",
            component:OrederCreate,
            meta:{title:"下订单"},
            props:true
        },
        // 订单列表
        {
            name:"orderList",
            path:"/orderList",
            component:OrderList,
            meta:{title:"订单列表"},
        },
        // 新闻资讯
        {
            name:"newLists",
            path:"/newLists",
            component:NewLists,
            meta:{title:"新闻资讯"},
        },
          // 新闻详细
          {
            name:"news_show",
            path:"/newsInfo/:id",
            component:NewsInfo,
            meta:{title:"新闻详情"},
            props:true
        },
        // 图片分享
        {
            name:"photoLists",
            path:"/photo/list",
            component:PhotoLists,
            meta:{title:"图片分享列表"},
        },
         // 图片详情
         {
            name:"photo_show",
            path:"/photo/show/:id",
            component:PhotoInfo,
            meta:{title:"查看图片"},
            props:true
        },
    ]
})
//路由全局前置守卫
 router.beforeEach((to,from,next)=>{
     if(to.meta.title){
        //  设置网页标题
         document.title=to.meta.title

     }
     next()
 })

export default router