<template>
  <div>
    <swiper :imgList="imgLists" />
    <ul class="mui-table-view mui-grid-view mui-grid-9">
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <routerLink to="/newLists">
        <img  src="../assets/images/menu1.png"/>
          <div class="mui-media-body">新闻资讯</div>
        </routerLink>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
         <routerLink to="/photo/list">
            <img  src="../assets/images/menu2.png"/>
          <div class="mui-media-body">图片分享</div>
        </routerLink>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
       <routerLink to="/category">
          <img  src="../assets/images/menu3.png"/>
          <div class="mui-media-body">商品购买</div>
       </routerLink>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <div @click="expect">
           <img  src="../assets/images/menu4.png"/>
          <div class="mui-media-body">留言反馈</div>
       </div>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <div @click="expect">
          <img  src="../assets/images/menu5.png"/>
          <div class="mui-media-body">视频专区</div>
         </div>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <div @click="expect">
          <img  src="../assets/images/menu6.png"/>
          <div class="mui-media-body">联系我们</div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import swiper from "../components/swiper";
import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      imgLists: []
    };
  },
  components: {
    swiper
  },

  mounted() {
    // 发送请求，请求图片 免费接口
    // .$indicator加载效果
    this.$indicator.open({ text: "图片加载中" });
    axios
      .get("https://api.github.com/search/users?q=b")
      .then(res => {
        console.log(res)
        this.$indicator.close();
        this.imgLists = res.data.items;
      })
      .catch(err => {
        this.$indicator.close();
        this.$toast("图片加载失败");
      });
  },
  methods: {
   expect(){
     this.$toast("敬请期待！")
   }
  },
};
</script>
<style lang="scss" scoped>
.mui-grid-view.mui-grid-9{
     background-color: #fff;
     border: none;  
    img{
        width:60px;
        height: 60px;
    }
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell{
     border: 0;
}
</style>


