<template>
  <div class="goods-info">
      <!-- 轮播图 -->
   <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
         <swiper :imgList="goodsinfo.swiperShow" />
        </div>
      </div>
    </div>
    <!--购物车小球动画效果 -->
    <transition @@before-enter="beforeEenter" @enter="Enter" @after-enter="afterEnter">
    <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!-- 商品购买 -->
    <div class="mui-card">
      <div class="mui-card-header">{{ goodsinfo.name }}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            定价：
            <span>￥{{ goodsinfo.price }}</span>
          </p>
          <div v-if="goodsinfo.num">
            <p class="go-num">
               购买数量:<numBox :max="goodsinfo.num" countVal="1" :goodsId="goodsinfo.id" :goodsName="goodsinfo.name"/>
            </p>
            <p class="go-buy">
              <mt-button type="primary" size="small" @click="goOrderCreate">立即购买</mt-button>
              <mt-button type="danger" size="small" @click="addCar">加入购物车</mt-button>
            </p>
          </div>
          <div v-else>该商品暂时无货</div>
        </div>
      </div>
    </div>
    <!-- 商品介绍 -->
    <div class="mui-card">
      <!-- 商品参数 -->
	<div class="mui-card-header">
        商品参数
    </div>
	<div class="mui-card-content">
         <div class="mui-card-content-inner">
        <p>商品热点:{{goodsinfo.hotspot}}</P>
        <p>库存情况:{{goodsinfo.num}}件</P>
        <p>上架时间:{{goodsinfo.time}}</P>
        </div>
    </div>
    <!-- 商品详情 -->
    <div class="mui-card-header">商品详情</div>
    <div class="good-desc">
        <div class="mui-card-content">{{goodsinfo.detail}}</div>
    </div>
	
    </div>
  </div>
</template>
<script>
import swiper from '../../components/swiper'
import numBox from '../../components/numBox'
export default {
  props: ["pid"],//路由跳转传递的参数 pid是商品的编号用来发起请求获取商品信息
  components:{
      swiper,
      numBox
  },
  data() {
    return {
      goodsinfo:{},//保存商品信息
      selectCount:1, //保存购买的商品数量
      ballFlag:false  //小球的是否显示
    };
  },
  created() {
    this.getGoodsInfo();
    // 全局事件总线 numBox普通组件触发传值 购买数量
    this.$bus.$on('PassVal',(value)=>{
        // console.log(value);
        //获取保存商品的购买数量
        this.selectCount=value.count
    })
  },
  methods: {
    getGoodsInfo() {
      //获取商品信息
      // console.log(this.pid)
      this.$http
        .get("posts", { params: { id: this.pid } })
        .then(res => {
          console.log(res);
          if (res.status >= 200 && res.status < 300) {
            this.goodsinfo = res.data[0];
          } else {
            this.$toast(res.statusText);
          }
        })
        .catch(err => {
          this.$toast("服务器异常！404");
        });
    },
    //加入购物车
    addCar() {
      // 存储到vuex里面
      // console.log(this.$store.state);
      this.ballFlag=!this.ballFlag  //小球显示
      //将购买的商品数量 存储到store中 
      this.$store.commit('shopcar/addShopCar',{
        id:this.pid,
        name:this.goodsinfo.name,
        count:this.selectCount,
        price:this.goodsinfo.price,
        max:this.goodsinfo.num,
        done:true,
        image:this.goodsinfo.image,
        time:this.goodsinfo.time
      })
      
    },
    // 动画效果的构子函数
    // el是添加动画效果的元素
    // 进入之前的小球位置
    beforeEenter(el){
      el.style.transform="translate(0,0)"
    },
    // 进入中
    Enter(el,done){
      // 获取小球位置 getBoundingClientRect()
    let ballPosition= this.$refs.ball.getBoundingClientRect()
      // 获取购物车位置 getBoundingClientRect()
      let badgePosition=document.getElementById('badge').getBoundingClientRect()
      let x=badgePosition.left-ballPosition.left
      let y=badgePosition.top-ballPosition.top
      el.style.transform=`translate(${x}px,${y}px)`
      el.style.transition = 'all .5s cubic-bezier(.4, -0.3, 1, .68)'
      //表明结束
      done()
    },
    // 进入后
    afterEnter(){
      this.ballFlag=!this.ballFlag  //隐藏小球
    },
    goOrderCreate(){
          //立即购买跳转到下订单页面
             // 查看有无填写地址和是否登录
        if(JSON.parse(localStorage.getItem('address'))!==null && JSON.parse(localStorage.getItem('userID'))!==null){
          // 添加商品状态为选择状态true
            this.goodsinfo.done=true
            //将立即购买的商品加入到结算车里面
            this.$store.commit('shopcar/setBuy',{
                  id:this.pid,
                  name:this.goodsinfo.name,
                  count:this.selectCount,
                  price:this.goodsinfo.price,
                  max:this.goodsinfo.num,
                  done:true,
                  image:this.goodsinfo.image,
                  time:this.goodsinfo.time
      })
            this.$router.push({name:'orderCreate',params:{id:this.goodsinfo.id}})
        }else{
              if(JSON.parse(localStorage.getItem('userID'))==null){
                this.$toast('请先登录')
                this.$router.replace('/user')
              }
             else if(JSON.parse(localStorage.getItem('address'))==null){
                 this.$toast('请填写收货地址')
                 this.$router.replace('/Address/add')
              }
        }
     

      
    }
  }
};
</script>
<style lang="scss" scoped>
.goods-info {
  background: #f1f1ff;
  overflow: hidden;
  .price {
    span {
      color: red;
      font-size: 14px;
      font-weight: bold;
    }
  }
  .go-buy {
    display: flex;
    margin: 10px 0 0px;
    justify-content: center;
    button {
      margin: 0 5px;
    }
  }
  .good-desc {
    background: #fff;
    padding: 5px;
    .mui-card-content{
        border-top: 1px solid gray;
        width: 95%;
        transform: translateX(10px);
    }
    h3 {
      font-size: 16px;
      color: #226aff;
      text-align: center;
      margin: 15px 0;
    }
    .content {
      font-size: 14px;
      line-height: 28px;
      img {
        width: 100%;
      }
    }
  }
  .go-num {
    div {
      margin-left: 10px;
    }
  }
  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    background: red;
    z-index: 99;
    left: 132px;
    top: 390px;
    transform: translate(93px, 230px);
  }
}
.mui-card-header:after{
    height: 0;
}
 .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    background: red;
    z-index: 99;
    left: 150px;
    top: 390px;
    transform: translate(93px, 230px);
  }
</style>


