<template>
     <div>
    <ul class="mui-table-view">
      <li class="mui-table-view-cell mui-media" v-for="item in newslist" :key="item.id">
        <router-link :to="{ name: 'news_show', params: { id: item.pid } }">
          <img class="mui-media-object mui-pull-left" :src="item.img_url">
          <div class="mui-media-body">
            <h1>{{item.title}}</h1>
            <p class='mui-ellipsis'>
              <span>发表时间：{{ item.add_time | dateFormat}}</span>
              <span>点击：{{ item.click }}次</span>
            </p>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
    data() {
        return {
            newslist:[]
        }
    },
    mounted() {
        // 获取新闻列表
        this.$http.get("posts/666").then(sucess=>{
            console.log(sucess);
            this.newslist=sucess.data.news
        }).catch(err=>{
            this.$toast('加载错误')
        })
    },
}
</script>
<style lang="scss" scoped>
.mui-table-view {
  li {
    h1 {
      font-size: 14px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: left;
    }
    .mui-ellipsis {
      font-size: 12px;
      color: #26a2ff;
      /* 实现左右布局 */
      display: flex;
      justify-content: space-between;
    }
  }
}
</style>


