<template>
  <div class="shopCar-container">
    <div class="mui-card">
      <div class="mui-card-content" v-for="item in car" :key="item.id">
        <div class="mui-input-row mui-checkbox mui-left">
          <label>&nbsp;</label>
          <input
            name="checkbox1"
            type="checkbox"
            :checked="item.done"
            @change="upDataDone(item.id)"
          >
        </div>
        <img src="../assets/images/phone.png">
        <div class="shopCar-text">
          <p class="name">{{item.name}}</p>
          <p class="price">{{item.price}}</p>
        </div>
        <div class="change-count">
          <numBox :max="item.num" :countVal="getGoodsCount[item.id]" :goodsId="item.id" size="min"/>
          <span @click="deleteGood(item.id)">删除</span>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-content acount-card">
        <div class="acount-left">
          <p>总计不含运费</p>
          <p>
            已勾选的商品
            <span>{{getSelectCounts}}</span>，总价
            <span>￥{{getGoodsPrice}}</span>
          </p>
        </div>
        <mt-button type="primary" class="acount-btn" @click="createOrder">去结算</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
// 解构获取vuex中的state
import { mapState, mapGetters } from "vuex";
import numBox from "../components/numBox.vue";
export default {
  components: {
    numBox
  },
  computed: {
    // 快速生成计算属性 获取ca购物车r
    ...mapState("shopcar", ["car"]),
    // 商品购买数量 选中的商品数量 选中的商品总价
    ...mapGetters("shopcar", [
      "getGoodsCount",
      "getSelectCounts",
      "getGoodsPrice"
    ]),
    // 获取用户地址
    ...mapGetters("user", ["getAddress"])
  },
  created() {
    this.upDataCar();
  },
  methods: {
    upDataCar() {
      //   console.log(this.car);
      // console.log(this.getGoodsCount);
      // 更新购物车的商品数量
      this.$bus.$on("PassVal", goodsinfo => {
        this.$store.commit("shopcar/updataGoodsInfo", goodsinfo);
      });
    },
    // 修改商品是否选中 id为用户勾选的商品id
    upDataDone(id) {
      // id传给store进行商品状态修改
      this.$store.commit("shopcar/upDataDone", id);
    },
    // 删除商品 id为用户点击的商品id
    deleteGood(id) {
      this.$store.commit("shopcar/deleteGoods", id);
    },
    createOrder() {
      // 创建订单
      // 是否选中商品
      if (this.getSelectCounts === 0) {
        this.$toast("您未选择商品结算");
      } else {
        //存储勾选的商品
        this.$store.commit("shopcar/setBuy");
        // 是否登录
        if (this.$auth.getUserId() === null) {
          this.$toast("请先登录！");
          this.$router.push("/user");
        } else {
          // 是否填写收货地址
          if (this.getAddress.length === 0) {
            this.$toast("请填写收货地址");
            this.$router.push("/Address/add");
          } else {
            // 跳转到下订单页面
            this.$router.replace({ name: "orderCreate", params: { id: 666 } });
          }
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.mui-card-content {
  display: flex;
  justify-content: flex-start;
  img {
    width: 20%;
  }
  .mui-input-row {
    width: 10%;
    display: flex;
  }
  .mui-checkbox input[type="checkbox"] {
    top: 30px;

    left: 10px;
  }
  .mui-radio label,
  .mui-checkbox label {
    padding: 0px;
  }
  .shopCar-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    p {
      margin: 0px;
      font-size: 16px;
    }
    .name {
      color: black;
    }
    .price {
      color: red;
      text-align: left;
    }
  }
  .change-count {
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    color: red;
    font-size: 16px;
    white-space: nowrap;
    span {
      transform: translate(10px, -7px);
    }
  }
}
.acount-card {
  justify-content: space-between;
  .acount-left {
    text-align: left;
    margin-left: 2%;
    margin-top: 4%;
    span {
      color: red;
      font-size: 18px;
    }
  }
  .mint-button--primary {
    align-self: flex-end;
    margin-bottom: 2%;
    margin-right: 2%;
  }
}
</style>
