<template>
 <div class="shopcart-container">
    <!-- 收货地址区域 -->
    <div class="address-container">
      <div class="mui-card">
        <!-- 跳转到地址列表进行地址选择 -->
        <router-link :to="{name: 'address'}" push>
          <h3>{{ address.user }} {{ address.phone }}</h3>
          <p>{{ address.area }} {{ address.det }}</p>
        </router-link>
      </div>
    </div>
    <!-- 商品列表 -->
    <div class="goods-list">
      <!-- 商品列表项区域 -->
      <div class="mui-card">
        <div class="mui-card-content" v-for="(item) in goodsList" :key="item.id">
          <div class="mui-card-content-inner flex">
            <img :src="item.image" />
            <div class="info">
              <h1>{{ item.name }}</h1>
              <p class="flex">
                <span class="price">¥{{ item.price }}</span>
                <span>x{{ item.counts }}</span>
              </p>
            </div>
          </div>
        </div>
        <!-- 配送信息 -->
        <div class="process-info">
          <p>
            <strong>配送服务</strong>
            <strong>快递运输</strong>
          </p>
          <p>
            <span>中小件送货时间</span>
            <span>工作日、双休日与节假日均可送货</span>
          </p>
        </div>
        <!-- 订单备注 -->
        <div class="store-info">
          <strong>订单备注</strong>
          <textarea v-model="note" type="text" placeholder="选填，给商家留言"></textarea>
        </div>
      </div>
    </div>
    <!-- 运费信息 -->
    <ul class="fare-info mui-card">
      <li class="fare-price flex">
        <span>商品金额</span>
        <span class="red">¥{{getGoodsPrice }}</span>
      </li>
      <li class="fare-price flex">
        <span>运费</span>
        <span class="red">¥0.00</span>
      </li>
      <li class="fare-price flex">
        <span>
          <strong>总价</strong>
        </span>
        <span class="red">¥{{ getGoodsPrice }}</span>
      </li>
      <!-- 创建订单按钮 -->
      <div class="flex">
        <button class="mui-btn mui-btn-primary mui-btn-block" @click="goOrderList">创建订单</button>
      </div>
    </ul>
  </div></template>
<script>
import { mapState, mapGetters } from "vuex";
export default {
  props:['id'],//存储选中收货地址的请求id
  data() {
    return {
      goodsList: [],//保存结算车中的商品信息
      note:"",
      address:this.$store.getters['user/getDefaultAddress']
    };
  },
  computed: {
    // 获取结算车
    ...mapState("shopcar", ["buy"]),
    // 获取商品状态为true的商品总价 状态为true的同件商品的总价 
    ...mapGetters("shopcar", ["getGoodsPrice", "getBuyCounts"]),
    
  },
  created() {
    this.getBuyGoods();
    this.selectAddress();
  },
  methods: {
    getBuyGoods() {
      // console.log(this.buy);
      // console.log(this.getGoodsPrice,this.getBuyCounts);
      // 获取结算车中商品的信息
      this.buy.forEach(item => {
        this.$http.get("posts", { params: { id: item.id } }).then(sucess => {
          //  console.log(sucess);
          // 保存结算车中的商品信息
          this.goodsList.push(sucess.data[0]);
          //生成相同商品的数量  将相同商品的数量添加进去
          this.goodsList.forEach(item => {
            // console.log(item.id);
            // 根据商品id来获取商品数量
            item.counts = this.getBuyCounts[item.id];
          });
        }).catch(err=>{
            this.$toast('商品数据丢失了')
        });
      });
    },
    selectAddress(){
        // 选择收货地址  
        // 如果有路由params参数id 
        if(this.id){
            // 发起请求获取要选择的地址
            this.$http.get('posts',{params:{id:this.id}}).then(sucess=>{
                this.address=sucess.data[0]
            }).catch(err=>{
                this.$toast('选择地址失败')
            })
        }
    },
    goOrderList(){
        this.$router.replace('/orderList')
    }
  }
};
</script>
<style lang="scss" scoped>
.flex {
  display: flex;
}
  

.shopcart-container {
  background: #eee;
  overflow: hidden;
  // 商品列表
  .goods-list {
    margin-top: 10px;
    .mui-card {
      margin: 0;
      .mui-card-content {
        border-bottom: 1px solid #eee;
        .mui-card-content-inner {
          align-items: center;
          padding: 10px;
          img {
            width: 100px;
          }
          .info {
            margin-left: 10px;
            box-sizing: border-box;
            width: 100%;
            overflow: hidden;
            h1 {
              font-size: 13px;
              font-weight: bold;
              line-height: 20px;
              padding-top: 22px;
            }
            p {
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              .price {
                font-size: 16px;
                font-weight: 700;
                color: red;
                flex: 1;
              }
            }
          }
        }
      }
      // 配送信息
      .process-info {
        padding: 10px;
        border-bottom: 1px solid #eee;
        p {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          color: #333;
          span {
            color: #999;
          }
        }
      }
      // 店铺备注
      .store-info {
        padding: 10px;
        textarea {
          font-size: 13px;
        }
      }
    }
  }
  // 运费信息
  .fare-info {
    padding: 10px;
    margin: 10px 0 0 0;
    .fare-price {
      padding: 5px 0;
      display: flex;
      justify-content: space-between;
      .red {
        color: red;
        font-size: 16px;
      }
    }
  }
  // 收货地址
  .address-container {
    .mui-card {
         text-align: left;
      margin: 0;
      padding: 10px;
      h3 {
        color: #333;
        font-size: 16px;
        font-weight: bold;
      }
    }
  }
}
</style>

