<template>
  <div class="login">
    <div class="mui-content">
      <form class="mui-input-group">
        <div class="mui-input-row">
          <label>账号</label>
          <input
            v-model="reg.username"
            type="text"
            class="mui-input-clear mui-input"
            placeholder="请输入账号"
          >
        </div>
        <div class="mui-input-row">
          <label>密码</label>
          <input
            v-model="reg.password"
            type="password"
            class="mui-input-clear mui-input"
            placeholder="请输入密码"
          >
        </div>
        <div class="mui-input-row">
          <label>确认</label>
          <input
            v-model="reg.comfirm"
            type="password"
            class="mui-input-clear mui-input"
            placeholder="请确认密码"
          >
        </div>
        <div class="mui-input-row">
          <label>邮箱</label>
          <input
            v-model="reg.email"
            type="text"
            class="mui-input-clear mui-input"
            placeholder="请输入邮箱"
          >
        </div>
      </form>
      <div class="mui-content-padded">
        <button @click="register" type="button" class="mui-btn mui-btn-block mui-btn-primary">登录</button>
        <div class="link-area">
          <p class="welcome">注册成功就去登录吧！</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "register",
  data() {
    return {
      // 注册的用户信息
      reg: {
        username: "",
        password: "",
        comfirm: "",
        email: ""
      }
    };
  },
  methods: {
    register() {
      // 注册
      //输入判断
      if (this.reg.username.trim().length > 5) {
        this.$toast("用户名不能大于5位");
        return;
      } else if (this.reg.username.trim() === "") {
        this.$toast("用户名不能为空");
        return;
      }
      if (this.reg.password.trim().length < 6) {
        this.$toast("密码不能小于6位");
        return;
      } else if (this.reg.username.trim() === "") {
        this.$toast("密码不能为空");
        return;
      }
      if (this.reg.password !== this.reg.password) {
        this.$toast("确认密码不一致");
        return;
      }
      if (
        !/[a-zA-Z0-9]+([-_.][A-Za-zd]+)*@([a-zA-Z0-9]+[-.])+[A-Za-zd]{2,5}$/.test(
          this.reg.email
        )
      ) {
        this.$toast("邮箱格式不正确");
        return;
      } else if (this.reg.email.trim() === "") {
        this.$toast("邮箱不能为空");
        return;
      }
      //发送请求 提交用户信息
      this.$http
        .post("posts", this.reg)
        .then(res => {
          if (res.status === 200 || res.status < 300) {
            // 存储到本地localStorage
            // 用户的唯一编号
            this.$auth.setUserId(res.data.id);
            // 用户名
            this.$auth.setUserName(res.data.username);
            // 用户信息存储到vuex中
            this.$store.commit("user/setUser", res.data);
            // 注册成功转到我的页面
            this.$router.push("/user");
          } else {
            //注册失败
            this.$toast(res.statusText);
          }
        })
        .catch(err => {
          this.$toast("注册失败");
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.welcome {
  text-align: center;
}
.mui-input-group {
  margin-top: 10px;
  background-color: transparent;
}
.mui-input-group label {
  width: 22%;
}
.mui-input-row:last-child {
  background: #fff;
}
.mui-input-row {
  margin-top: 20px;
  background: #fff;
}
.mui-input-row label ~ input,
.mui-input-row label ~ select,
.mui-input-row label ~ textarea {
  width: 78%;
}
.link-area {
  display: block;
  margin-top: 25px;
  text-align: center;
}
.mui-content-padded {
  margin-top: 30px;
}
</style>


