import axios from 'axios'


export default{
    install(vue){
        //创建一个axios
        const result=axios.create({
            // 配置基本路径 json-server服务器
                baseURL:'http://localhost:3000/'
            })
            // 请求拦截器
        //原型添加属性$http 简便使用
    vue.prototype.$http=result
    }
}

  
