<template>
  <div id="app">
    <div class="container">
      <mt-header fixed :title="$route.meta.title">
        <span slot="left" v-show="isShow">
          <mt-button icon="back" @click="goBack">返回</mt-button>
        </span>
      </mt-header>
      <!-- <router-link to="/"></router-link>
      <router-link to="/category"></router-link>-->
      <transition name="toggle">
        <router-view>
          <router-view></router-view>
        </router-view>
      </transition>
      <tabbar/>
    </div>
  </div>
</template>

<script>
import tabbar from "./components/tabbar";
export default {
  name: "App",
  data() {
    return {
      isShow: false
    };
  },
  mounted() {
    //防止刷新页面数丢失 vuex中的数据丢失 保存登录状态 再次存储vuex中
    if (this.$auth.getUserId() !== null) {
      this.$http
        .get("posts", { params: { id: this.$auth.getUserId() } })
        .then(res => {
          this.$store.commit("user/setUser", res.data);
        });
    }
  },
  watch: {
    // 监视页面路径
    "$route.path": {
      immediate: true, //初始化就调用handler渲染
      handler(newPath, oldPaht) {
        this.isShow = newPath !== "/home";
      }
    }
  },
  methods: {
    goBack() {
      // 退后
      this.$router.go(-1);
    }
  },
  components: {
    tabbar
  }
};
</script>

<style lang="scss" scope>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  box-sizing: border-box;
}
.container {
  padding-top: 40px;
  padding-bottom: 50px;
  overflow: hidden;
}
// 进入和离开过渡动画效果
.toggle-enter {
  transform: translateX(100%);
}
.toggle-leave-to {
  transform: translateX(-100%);
  position: absolute;
  opacity: 0;
}
.toggle-enter-active,
.toggle-leave-active {
  transition: all 0.5s ease;
}
.toggle-leave {
  transform: translateX(0);
}

</style>


 

