<template>
  <div class="goodsList-container">
    <div class="mui-card" v-for="(item) in getBuy" :key="item.id">
      <div class="mui-card-header">
        <div class="tiem">{{item.time}}</div>
        <div>
          <span>未支付</span> |
          <span @click="cancel(item.id)" >{{item.cancel}}</span>
        </div>
      </div>
      <div class="mui-card-content">
        <img :src="item.image">
        <span class="counts">x{{item.count}}</span>
      </div>
      <p>
        实付金额：
        <span class="price">￥{{item.price}}</span>
      </p>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    //结算商品的信息 取消
    ...mapGetters("shopcar", ["getBuy",'cancelOrder'])
  },
  mounted() {
    // console.log(this.getBuy);
    this.goodsInfo();
  },
  methods: {
    goodsInfo() {
      //   添加属性 商品总额
      this.getBuy.forEach(item => {
        //  计算出同件商品的总额
        item.price = item.count * item.price;
      });
    },
    cancel(id){
        //结算商品遍历 取消订单
         this.getBuy.forEach(item => {
            // 通过用户点击传递的商品id来判断是否取消订单
             if(item.id===id){
              //  修改取消结算的商品的状态
                 item.done=!item.done
                //  将取消结算的商品id传递给store
                  this.$store.commit('shopcar/cancelOrder',id)
             }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.mui-card {
  color: gray;
  .mui-card-header {
    font-size: 16px;
  }
  .mui-card-header:after {
    right: 7px;
    left: 7px;
  }
  .mui-card-content {
    display: flex;
    justify-content: flex-start;
    img {
      width: 150px;
    }
    span {
      text-align: left;
      align-self: flex-end;
      padding: 0 0 6% 0;
    }
  }
   p {
      align-self: flex-end;
      margin-right: 20px;
      white-space: nowrap;
        float: right;
      .price {
        color: black;
        font-size: 18px;
      }
    }
}
</style>


