<template>
  <div class="address-container">
    <div class="mui-card" v-for="(item,index) in address" :key="index">
      <div class="mui-card-content">
        <div class="addressInfo" @click=" selectBuyAddress(item.id)">
          <p class="p1">{{item.user}} {{item.phone}}</p>
          <p class="p2">{{item.area}} {{item.det}}</p>
        </div>

        <router-link :to="{name:'addressEdit',params:{id:item.id}}" class="edit">编辑</router-link>
      </div>
    </div>
    <div class="mod_btns fixed">
      <router-link to="/Address/add" class="mod_btn">新增收货地址</router-link>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      address:
        JSON.parse(localStorage.getItem("address")) ||
        this.$store.getters["user/getAddress"] // 获取收货地址
    };
  },
  computed: {
    ...mapGetters("shopcar", ["getSelectCounts"]),
    ...mapGetters("user", ["getAddress"])
  },
  mounted() {},
  methods: {
    selectBuyAddress(id) {
      // 购物车中有商品才能触发      用于给下订单页面选择收货地址
      if (this.getSelectCounts !== 0) {
        // 传递选择的地址的请求id
        this.$router.replace({
          name: "orderCreate",
          params: { id }
        });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.address-container {
  background: #eee;
  overflow: hidden;
  .mod_btn {
    font-size: 16px;
    line-height: 46px;
    height: 46px;
    text-align: center;
    background: #3884ff;
    flex: 1;
    display: block;
    color: #fff;
  }
  .mod_btns .fixed {
    display: flex;
    overflow: hidden;
  }
}
.mui-card-content {
  display: flex;
  justify-content: space-between;
  text-align: left;
  .p1 {
    font-size: 18px;
    margin-bottom: 0px;
    margin-top: 20px;
    padding-left: 5%;
    white-space: nowrap;
  }
  .p2 {
    font-size: 10px;
    margin-left: 5%;
    white-space: normal;
  }
  .edit {
    font-size: 16px;
    color: red;
    align-self: flex-end;
    margin-bottom: 15px;
    margin-right: 5%;
  }
}
</style>

